
/* navbar */

.navbar{
    position: fixed;
    width: 100%; 
    /* left: 50%; 
    top: 10px; */
    padding: 15px 30px;
    /* transform: translateX(-50%); */
    z-index: 5;
    color: white;

}
.navbar.scrolled{
    background-color: var(--ebony_purply_blue);
    width: 100vw; 
    left: 0; 
    top: 0;
    transform: translateX(0);
    animation: appeandNav 0.5s 1 forwards;
    padding: 15px 20px;
}
.navbar img{
    height: 50px;

}
.navbar ul li{
    position: relative;
    cursor: pointer;
    padding: 3px;
}
.navbar ul li::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: var(--light_color) ;
    transition: .3s;

}

.navbar li:hover::after {
    width: 100%;
}
.number{
    font-weight: 700;
    font-size: var(--font-size-xl);
    cursor: pointer;
}

.number .icon-call{
    animation: callPhone 0.5s infinite;
    font-size: var(--font-size-2xl);
}


@keyframes appeandNav {
    from{
        top : -100px
    }to{
        top : 0
    }
    
}
@keyframes callPhone {
    0% { transform: translate(1px, 0); }
    20% { transform: translate(-1px, 0); }
    40% { transform: translate(1px, 0); }
    60% { transform: translate(-1px, 0); }
    80% { transform: translate(1px, 0); }
    100% { transform: translate(0, 0); }
    
}

@keyframes viewUl    {
    from{
        opacity: 0;
    }to{
        opacity: 1;
    }
    
}
@keyframes viewMenu {
    from{
width: 0;
    }to{
width: 40%;
    }
    
}
/* ---------------------End navbar------------------------- */
/* hero */
.hero{
    background: linear-gradient(135deg, var(--ebony_purply_blue), var(--dark_purply_blue), var(--whyskey_yellowish_orange )); 
    color: white;
    overflow: hidden;
}

.image-changes{
    position: relative;
    
}

.shap_1{
    width: 600px;
    height: 400px;
    background-color:  var(--whyskey_yellowish_orange );
    opacity: 0.6;
    transform: rotate(210deg);
    border-radius: 20px;
    position: absolute;
    z-index: 2;
    top: -500px;
    animation: insertShap_1 forwards 0.5s 1;
}
.shap_2{
    width: 300px;
    height: 500px;
    background-color:var(--dark_purply_blue)  ;
    opacity: 0.5;
    transform: rotate(210deg);
    border-radius: 20px;
    position: absolute;
    z-index: 2;
    top: 0;
    right: -500px;
    animation: insertShap_2 forwards 0.5s 1;
    animation-delay: 0.4s;
}
.shap_3{
    width: 300px;
    height: 500px;
    background-color:var(--dark_purply_blue)  ;
    opacity: 0.5;
    transform: rotate(410deg);
    border-radius: 20px;
    position: absolute;
    z-index: 2;
    bottom: 100px;
    left: -500px;
    animation: insertShap_3 forwards 0.5s 1;
    animation-delay: 0.4s;
}

.hero img{
    border-radius: 100px;
    filter: grayscale(0.3);
    width: 100%;
    opacity: 0;
    animation: appendImage .5s forwards 1;
}
@keyframes appendImage {
    from{
        opacity: 0;
        transform: scale(0.5);
    }
    to{
        opacity: 1;
        transform: scale(1);
    }
}
@keyframes insertShap_1 {
    from{
        top: -500px;
    }
    to{
        top:-200px
    }
}
@keyframes insertShap_2 {
    from{
        right: -500px;
    }
    to{
        right:-100px
    }
}
@keyframes insertShap_3 {
    from{
        left: -500px;
    }
    to{
        left:-200px
    }
}
