    padding: 10px;
  }

  /* types */
  .types{
    background-color: var(--ebony_purply_blue);
    border-top-left-radius: 5rem;
    border-top-right-radius: 5rem;
  }
  .types h3{
    font-weight: 600;
    color: var(--whyskey_yellowish_orange);
    
  }
  
  .card{
    position: relative;
    background-color: white;
    color: black;
    overflow: hidden;
    padding: 20px;
    height: 400px;
    border-radius: 8px;

  }
  .card p{
    opacity: 0;
  }
  .card:hover img {
    position: absolute;
    top: 50%;        /* Start at center */
    right: 50%;      /* Start at center */
    transform: translate(-50%, -50%) scale(1); /* Centered and at original scale */
    transition: top 0.5s, right 0.5s, transform 0.5s; /* Smooth transition */
}

.card img {
    top: 0;          /* Move to the top */
    right: 0;        /* Move to the top-right */
    transform: scale(1) translate(0, 0); /* Smaller size at the top */
}
  .card:hover p{
    animation: fadeUp 0.5s forwards; 
    animation-delay: 0.2s;
  }

  @keyframes fadeUp {
    from{
        /* transform: translateY(500px); */
        opacity: 0;
    }to{
        /* transform: translateY(-240px); */
        opacity: 1;
    }
  }

