/* Steps */
.steps h3{
    font-weight: 700;
    color: black;
    
}
.card-step{
    position: relative;
    overflow: hidden;
    box-shadow: 0 30px 100px 0 rgba(0, 0, 0, .08);
    padding: 30px;
    margin: 0 20px;
    border-radius: 8px;
    background: linear-gradient(to right, var(--whyskey_yellowish_orange), var(--whyskey_dark_orange));
    min-height: 300px;
    transition: 0.3s;
}
.card-step:hover{
    box-shadow: 0 30px 10px 0 rgba(0, 0, 0, 0.233);
}
.card-step span{
    color: white;
    font-size: 50px;
    padding: 10px 0;
    transition: 0.5s;
}

.card-step  h4{
    font-weight: 600;
    color: var(--ebony_purply_blue);
    padding: 10px 0;
}
.card-step .content-step {

    padding: 10px;
    color: var(--ebony_purply_blue);
    letter-spacing: 0px;
    position: absolute;
    top: 400px;
    left: 5px;
    bottom: 0;
    right: 5px;
    border-radius: 8px;
    background-color: white;
    
}
.card-step:hover .content-step{
    animation: appendP 0.5s 1 forwards ;
}
