.whatsapp-icon {
    margin: 20px;
    position: fixed;
    bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #25D366;
    right: 0px;
    color: white;
    width: 70px;
    height: 70px;
    overflow: hidden;
    border-radius:50%;
    cursor:pointer;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    z-index:11;
    cursor: pointer;
}

@keyframes shine {
    0% {
        left: -75%;
    }
    50% {
        left: 25%;
    }
    100% {
        left: 125%;
    }
}

.whatsapp-icon::before {
    content: "";
    position: absolute;
    top: 0;
    z-index:202;
    width: 100%;
    height: 100%;
    cursor:pointer;
    background: linear-gradient(to right, 
        rgba(255, 255, 255, 0) 0%, 
        rgba(255, 255, 255, 0) 25%, 
        rgba(255, 255, 255, 0.8) 50%, 
        rgba(255, 255, 255, 0) 75%, 
        rgba(255, 255, 255, 0) 100%);
    animation: shine 1.3s linear infinite;
}
