@media(max-width: 1024px){
    .shap_1{
        background-color:  var(--dark_purply_blue );
    }

    .navbar{
        width: 100vw; 
        left: 0; 
        top: 0;
        transform: translateX(0%);
    }
    .navbar .menu{
        display: none;
    }
    .navbar .menu ul{
        display: block;
        opacity: 0;
    }
    .navbar .menu ul li{
        text-align: center;
    }
    .navbar .view{
        display: block;
        position: fixed;
        width: 0;
        background-color: var(--whyskey_yellowish_orange);
        left: 0;
        top: 0;
        height: 100vh;
        z-index: 10;
        animation: viewMenu 0.3s 1 forwards;
    }
    .navbar .view ul{
        animation: viewUl 0.4s 1 forwards;
        animation-delay: 0.2s;
        opacity: 0;
    }
    .next-steps .steps-number{
        font-size: 100px;
    }
    h3{
        text-align: center
    }
