@import url("https://fonts.googleapis.com/css2?family=Arima:wght@100..700&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@import url("./variables.css");
@import url("./landingPage.css");

/* Tailwind CSS */
@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  /* background-color: var(--light_color) ; */
}
h1{
  font-size: var(--font-size-3xl);
}
h2{
  font-size: var(--font-size-xxl);
}
h3{
  font-size: var(--font-size-xl);
}
h4{
  font-size: var(--font-size-l);
}
h5{
  font-size: var(--font-size-s);
}

p{
  font-size: var(--font-size-s);
}
.text-error{
  color: var(--error-color);
}
.text-success{
  color: var(--success-color);
}
.text-warning{
  color: var(--warning-color);
}
.bg-error{
  background-color: var(--error-color);
}
.bg-success{
  background-color: var(--success-color);
}
.bg-warning{
  background-color: var(--warning-color);
}

button.outline-blue{
  background-color: var(--light_color);
  color: var(--dark_purply_blue);
  border: 2px solid var(--dark_purply_blue);
  border-radius: 8px;
  padding: 10px;
  font-weight: 500;
  transition: 0.3s;
}
button.outline-blue:hover{
  background-color:var(--dark_purply_blue) ;
  color:var(--light_color) ;
  border: 2px solid var(--light_color);
}
